<template>
  <v-app>
    <v-app-bar app color="primary" dark flat>
      <div class="d-flex align-center flex-shrink-0"  @dblclick="openTerminal">
        <!-- <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
          transition="scale-transition"
          width="40"
        />-->
        <v-icon class="mr-2">mdi-bluetooth</v-icon>
        <h1 class="text-h6" style="font-weight: 900;">BLX-PROTOTYPE</h1>
      </div>

      <v-spacer></v-spacer>

      <v-btn text class="ml-auto mr-n4" @click="installApp" v-if="notInstalled">
        <v-icon class="mr-2">mdi-arrow-down-bold-circle</v-icon>
        <span class="mr-2 d-none d-sm-block">Install App</span>
      </v-btn>
      <v-btn color="accent" @click="openTerminal">Open Terminal</v-btn>
    </v-app-bar>

    <v-main>
      <v-fade-transition>
        <v-alert
        v-if="!checkBluetoothAvailability && !bluetoothAvailable"
          border="right"
          class="ma-4"
          elevation="1"
          type="error"
        >Bluetooth is not supported in your browser.</v-alert>
      </v-fade-transition>
      <router-view :isBluetoothAvailable="bluetoothAvailable" />
    </v-main>
    <Terminal :open="terminalOpen" :closeTerminal="() => {terminalOpen = false}"></Terminal>
    <v-footer color="secondary">
      <v-spacer></v-spacer>
      <div>&copy; {{ new Date().getFullYear() }}</div>
    </v-footer>
  </v-app>
</template>

<script>
import Terminal from './components/Terminal.vue'
export default {
  name: 'App',

  components: { Terminal },

  data: () => ({
    terminalOpen: false,
    notInstalled: true,
    checkBluetoothAvailability: true,
    bluetoothAvailable: false
  }),

  created () {
    if (typeof window.navigator.bluetooth === 'undefined') {
      this.checkBluetoothAvailability = false
      this.bluetoothAvailable = false
    }

    navigator.bluetooth
      .getAvailability()
      .then(isBluetoothAvailable => {
        this.bluetoothAvailable = isBluetoothAvailable
      })
      .finally(() => {
        this.checkBluetoothAvailability = false
      })

    if ('onavailabilitychanged' in navigator.bluetooth) {
      navigator.bluetooth.addEventListener('availabilitychanged', function (
        event
      ) {
        this.bluetoothAvailable = event.value
        console.log(
          `> Bluetooth is ${event.value ? 'available' : 'unavailable'}`
        )
      })
    }
  },

  methods: {
    openTerminal () {
      this.terminalOpen = true
    },
    installApp () {
      this.notInstalled = false
    }
  }
}
</script>

<style>
#app {
  background-color: #fcfcfc;
  color: white;
}
</style>
