import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const localStoragePrefix = 'BLX_2020_'

export default new Vuex.Store({
  state: {
    isBluetoothAvailable: false,
    currentServer: null,
    devices: [
      { name: 'TestgerÃ¤t', id: 'xyz', log: [], lastSeen: new Date().toISOString(), lastFetch: new Date().toISOString(), data: [] },
      { name: 'GerÃ¤t 3', id: 'xyz', log: [], lastSeen: new Date().toISOString(), lastFetch: new Date().toISOString(), data: [] }
    ]
  },
  mutations: {
    initialiseStore (state) {
      const devicesStr = localStorage.getItem(localStoragePrefix + 'devices')
      if (typeof devicesStr === 'string') {
        try {
          const devices = JSON.parse(devicesStr)
          if (Array.isArray(devices)) {
            state.devices = devices
          }
        } catch (error) {}
      }
    },
    upsertDevice (state, device) { // insert or update device
      if (device) {
        const foundIdx = state.devices.findIndex(el => el.name === device.name && el.id === device.id)
        if (foundIdx !== -1) {
          // update old version
          const newArr = [...state.devices]
          newArr[foundIdx] = device
          state.devices = newArr
        } else {
          // add new
          state.devices = [...state.devices, device]
        }
        localStorage.setItem(localStoragePrefix + 'devices', JSON.stringify(state.devices))
      }
    },
    removeDevice (state, deviceId) {
      // TODO: Snackbar with undo operation (click triggers timeout. Only after timeout will it delete the item for real)
      state.devices.splice(deviceId, 1)
      state.devices
        .localStorage.setItem(localStoragePrefix + 'devices', JSON.stringify(state.devices))
    },
    setCurrentServer (state, server) {
      if (server) {
        state.currentServer = server
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
