<template>
  <v-container>
    <v-row class="text-center">
      <v-col>
        <v-card flat class="mx-auto" tile>
          <v-list outlined class="mb-2 pt-0">
            <v-list-item
              two-line
              :ripple="!isBluetoothAvailable"
              :disabled="!isBluetoothAvailable"
              @click="addBleDevice"
              class="accent--text font-weight-black"
              color="accent"
            >
              <v-list-item-avatar>
                <v-icon class="accent white--text">mdi-plus</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="accent--text font-weight-black text-left">Live Connection</v-list-item-title>
                <v-list-item-subtitle
                  class="accent--text font-weight-regular text-left"
                >Search for a new Bluetooth device and open a live connection</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-subheader>STORED DEVICES</v-subheader>
            <v-list-item-group v-model="selectedDevice" color="primary">
              <v-list-item @dblclick="openDevice($event,i)" v-for="(device, i) in devices" :key="i">
                <v-list-item-icon>
                  <v-icon>mdi-bluetooth</v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title class="text-left" v-text="device.name"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
        <v-fade-transition>
          <v-row dense v-if="typeof selectedDevice === 'number'">
            <v-col cols="8" sm="10">
              <v-btn
                :loading="connectingToDevice"
                @click="connectToDevice"
                color="accent"
                depressed
                block
              >Read data</v-btn>
            </v-col>
            <v-col cols="4" sm="2">
              <v-btn block depressed color="error">
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-fade-transition>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
// BLE-CMDs - BLE JoWi's I/O -----
const NUS_SERVICE_UUID = '5c170001-b5a3-f393-e0a9-a37f42997c22'
const NUS_DATA_CHARA_WRITE = '5c170002-b5a3-f393-e0a9-a37f42997c22'
const NUS_DATA_CHARA_READ_NOTI = '5c170003-b5a3-f393-e0a9-a37f42997c22' // fuer Notifications

export default {
  name: 'Landing',

  data: () => ({
    selectedDevice: null,
    connectingToDevice: false
  }),
  computed: {
    devices () {
      return this.$store.state.devices
    }
  },
  methods: {
    delteDevice () {
      this.$store.commit('removeDevice', this.selectedDevice)
    },
    openDevice (ev, i) {
      this.$router.push('device/' + this.devices[i].id)
    },
    async connectToDevice () {
      this.connectingToDevice = true

      try {
        const device = this.devices[this.selectedDevice]
        const server = await device.gatt.connect()
        // const nusService = await server.getPrimaryService(NUS_SERVICE_UUID)
        //   const dataCharaWrite = await nusService.getCharacteristic(NUS_DATA_CHARA_WRITE)
        //   const readNoti = await nusService.getCharacteristic(NUS_DATA_CHARA_READ_NOTI)
        //   await readNoti.startNotifications()
        //   readNoti.addEventListener('characteristicvaluechanged', onBlxNUSData)
        //   onBlxConnected()
        this.$router.push('device/' + this.devices[this.selectedDevice].id)
      } catch (error) {
        console.log(error)
        // TODO: Add error-management. Maybe snackbar?
      } finally {
        this.connectingToDevice = false
      }
      // await new Promise(resolve => setTimeout(resolve, 1500))
    },
    async addBleDevice () {
      if (!this.isBluetoothAvailable) return
      try {
        const device = await navigator.bluetooth.requestDevice({
          filters: [{ services: [NUS_SERVICE_UUID] }]
        })
        const storedDevice = {
          name: device.name,
          id: device.id,
          log: [],
          lastSeen: new Date().toISOString(),
          lastFetch: null,
          data: []
        }
        this.$store.commit('setCurrentServer', device)
        this.$store.commit('upsertDevice', storedDevice)
        this.$router.push('device/' + device.id)
      } catch (error) {
        console.warn(error)
      }
    }
  },
  props: ['isBluetoothAvailable']
}
</script>

<style>
.v-list-item.v-list-item--disabled * {
  color: gray !important;
}

.v-list-item.v-list-item--disabled {
  background-color: #eeeeee;
}
